@import "https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:ital,wght@0,100..700;1,100..700&display=swap";
h1 {
  font-family: Roboto, sans-serif;
  font-weight: 400;
}

@font-face {
  font-family: Roboto, Arial, Helvetica, sans-serif;
  src: url("Roboto-Regular-webfont.55e67705.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

html, body, div, span, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, code, del, dfn, em, img, q, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td {
  font-weight: inherit;
  font-style: inherit;
  border: 0;
  margin: 0;
  padding: 0;
  font-family: inherit;
  font-size: 100%;
}

em {
  font-style: italic;
}

strong {
  font-weight: bold;
}

:root {
  box-sizing: border-box;
  font-family: Montserrat, sans-serif;
}

html, body {
  scroll-behavior: smooth;
  background: #eee;
  height: 100%;
  margin: 0;
  padding: 0;
  position: relative;
}

li {
  list-style: none;
}

a {
  color: #0f0e08;
  text-decoration: none;
}

.overlay {
  background-color: #0000;
  transition: background-color .3s;
}

.overlay--active {
  z-index: 2;
  background-color: #00000075;
  width: 100%;
  height: 100%;
  position: absolute;
}

.navigation {
  color: #fff;
  z-index: 3;
  background-color: #1a5a4c;
  justify-content: center;
  align-items: start;
  gap: 220px;
  width: 100vw;
  height: 0;
  font-family: IBM Plex Sans, sans-serif;
  transition: height .3s;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  overflow: hidden;
}

.navigation__left, .navigation__right {
  margin: 40px 0 48px;
}

.navigation__paragraph {
  margin-bottom: 20px;
  font-size: 14px;
  font-weight: 400;
  line-height: 140%;
}

.navigation__lists {
  align-items: start;
  gap: 30px;
  display: flex;
}

.navigation__grid {
  grid-template-columns: repeat(2, 1fr);
  gap: 30px;
  display: grid;
  position: relative;
}

.navigation__grid:after {
  content: "";
  background-color: #fff;
  width: 1px;
  height: 145px;
  position: absolute;
  top: 0;
  right: -102px;
}

.navigation__link {
  color: #fff;
  font-size: 20px;
  font-weight: 500;
}

.navigation__element, .navigation__paragraphs {
  font-size: 14px;
}

.navigation__paragraphs {
  max-width: 170px;
}

.navigation--active {
  height: 233px;
}

.art-museum {
  justify-content: space-between;
  align-items: start;
  gap: 30px;
  margin-bottom: 200px;
  display: flex;
  position: relative;
}

.art-museum__left {
  padding: 39px 0 0 55px;
}

.art-museum .burger-menu {
  z-index: 15;
  cursor: pointer;
  flex-direction: column;
  gap: 3px;
  width: 18px;
  height: 12px;
  margin-bottom: 143px;
  display: flex;
  position: relative;
}

.art-museum .burger-menu__line {
  background-color: #0f0e08;
  width: 100%;
  height: 2px;
  transition: all .3s;
}

.art-museum .burger-menu--active span {
  background-color: #fff;
}

.art-museum .burger-menu--active span:nth-of-type(2) {
  display: none;
}

.art-museum .burger-menu--active span:first-of-type {
  transform-origin: center;
  transform: rotate(-45deg)translateY(3.5px);
}

.art-museum .burger-menu--active span:last-of-type {
  transform-origin: center;
  transform: rotate(45deg)translateY(-3.5px);
}

.art-museum__right {
  width: 100%;
}

.art-museum__text {
  position: relative;
}

.art-museum__title {
  margin-bottom: 90px;
  font-size: 72px;
  font-weight: 700;
  line-height: 120%;
}

.art-museum__events {
  color: #687480;
  font-family: IBM Plex Sans, sans-serif;
  font-size: 14px;
  font-weight: 700;
  display: inline-block;
  position: absolute;
  top: 322px;
  left: 0;
  transform: rotate(-90deg)translateY(-100%);
}

.art-museum__events:before {
  content: "";
  background-color: #1a5a4c;
  width: 70px;
  height: 1px;
  position: absolute;
  top: 50%;
  left: calc(-100% - 10px);
  transform: translateX(-50%);
}

.art-museum__order-tickets {
  border: 1px solid #1a5a4c;
  justify-content: space-between;
  align-items: center;
  width: 270px;
  height: 70px;
  margin-left: auto;
  display: flex;
  position: relative;
}

.art-museum__tickets {
  color: #0f0e08;
  text-align: center;
  width: 200px;
  height: 70px;
  font-family: IBM Plex Sans, sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 70px;
  position: relative;
}

.art-museum__arrow-right {
  background-color: #1a5a4c;
  width: 70px;
  height: 70px;
  position: relative;
}

.art-museum__arrow-right:after {
  content: "";
  background: url("arrow-right.4f1b2c4e.svg") center / cover no-repeat;
  width: 7.41px;
  height: 12px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.museum__visual {
  aspect-ratio: 374 / 384;
  background: url("museum.bfc6ba76.jpg") center / cover no-repeat;
  width: 100%;
  height: auto;
  position: relative;
}

.museum__text-logo {
  font-size: 36px;
  font-weight: 700;
  position: absolute;
  top: 15px;
  left: 50px;
}

@media screen and (width >= 320px) and (width <= 767px) {
  .navigation {
    box-sizing: border-box;
    flex-direction: column;
    gap: 50px;
    width: 100vw;
    padding: 0 20px;
  }

  .navigation__left {
    margin: 27px 0 0 52px;
  }

  .navigation__right {
    margin: 0 0 27px;
  }

  .navigation__lists {
    flex-direction: column;
    gap: 16px;
  }

  .navigation__paragraphs {
    max-width: 144px;
  }

  .navigation__grid {
    grid-template-columns: repeat(2, 100px);
  }

  .navigation__grid:after {
    width: calc(100vw - 40px);
    height: 1px;
    top: auto;
    bottom: -24px;
    left: -52px;
  }

  .navigation__link {
    font-size: 16px;
  }

  .navigation--active {
    height: auto;
  }

  .art-museum {
    flex-direction: column-reverse;
    margin-bottom: 70px;
    padding: 0;
    position: relative;
  }

  .art-museum__left {
    width: calc(100% - 40px);
    padding: 0 20px;
  }

  .art-museum__title {
    text-align: center;
    margin-bottom: 20px;
    font-size: 45px;
  }

  .art-museum__order-tickets {
    justify-content: center;
    width: 100%;
    height: 50px;
    margin-left: 0;
  }

  .art-museum__tickets {
    text-align: center;
    width: calc(100% - 50px);
    height: 50px;
    padding: 0;
    line-height: 50px;
  }

  .art-museum__arrow-right {
    width: 50px;
    height: 50px;
  }

  .art-museum__events {
    display: none;
  }

  .museum__text-logo {
    left: 50%;
    transform: translateX(-50%);
  }

  .burger-menu {
    position: absolute;
    top: 27px;
  }
}

@media screen and (width >= 768px) and (width <= 1280px) {
  .navigation {
    gap: 77px;
  }

  .navigation__left {
    margin-left: 96px;
  }

  .navigation__right {
    margin-right: 34px;
  }

  .navigation__paragraphs {
    max-width: 144px;
  }

  .navigation__grid {
    grid-template-columns: repeat(2, 100px);
  }

  .navigation__grid:after {
    right: -38px;
  }

  .navigation__link {
    font-size: 16px;
  }

  .art-museum {
    margin-bottom: 88px;
  }

  .art-museum__left {
    padding: 45px 0 0 37px;
  }

  .art-museum__title {
    font-size: 54px;
  }

  .art-museum__order-tickets {
    margin-left: 0;
  }

  .art-museum__events {
    display: none;
  }

  .burger-menu {
    margin-bottom: 70px;
  }
}

